import { Button, Box, Text, Image, Skeleton, Flex, Popover, PopoverTrigger, PopoverContent, PopoverArrow, PopoverCloseButton, PopoverHeader, PopoverBody, useToast } from "@chakra-ui/react";
import { podoContractAddress } from "../../contracts"
import { useEthers, useTokenBalance } from "@usedapp/core";
import { formatUnits } from '@ethersproject/units'
import podo from "../../res/PODO.png"
import { useEffect } from "react";
import { useMint } from "../../hooks/podo";
import { Link } from "@reach/router";

export default function PodoBalance() {
    const toast = useToast();
    const { account } = useEthers();
    const podobalance = useTokenBalance(podoContractAddress, account)
    const { state, send } = useMint();
    const getPodo = () =>{
        send(account, 100)
    }
    useEffect(()=>{
        if(state.status === "Mining") {
            toast({
                title: " ",
                position: "top-right",
                description: " .",
                status: "info",
                duration: 9000,
                isClosable: true,
              })
        }
        if(state.status === "Success") {
            toast({
                title: "  ",
                position: "top-right",
                description: "  !",
                status: "success",
                duration: 9000,
                isClosable: true,
              })
        }
        if(state.status === "Fail") {
            toast({
                title: " ",
                position: "top-right",
                description: state.errorMessage,
                status: "error",
                duration: 9000,
                isClosable: true,
              })
        }
        if(state.status === "Exception") {
            toast({
                title: "  ",
                position: "top-right",
                description: state.errorMessage,
                status: "error",
                duration: 9000,
                isClosable: true,
              })
        }
    },[state])
    return (
            <Popover>
            <PopoverTrigger>
                <Flex
                    cursor="pointer"
                    alignItems="center"
                    justifyContent="center"
                    mx="10px"
                    display={{ base: 'none', md: 'flex' }}
                >
                <Image src={podo} height="2rem" width="2rem" />
                <Text 
                    color="purple.500"
                    fontSize="rm"
                    fontWeight="semibold"
                    marginLeft="2"
                >
                    {!podobalance ? <Skeleton w="50px" h="30px" />: <Text>{formatUnits(podobalance, 0)}</Text>}
                </Text>
                </Flex>
            </PopoverTrigger>
            <PopoverContent
                w="100%"
                h="100%"
            >
                <PopoverArrow />
                <PopoverCloseButton />
                <PopoverHeader> !</PopoverHeader>
                <PopoverBody>
                    <Button
                        px="10"
                        backgroundColor="purple.500"
                        color="white"
                        onClick={getPodo}
                    >
                        !
                    </Button>
                </PopoverBody>
            </PopoverContent>
            </Popover>
    );
}