import {
    Box,
    Textarea,
    Flex,
    Text,
    Input,
    useToast,
    Button,
  } from "@chakra-ui/react";
import React, { useEffect } from "react";
import MainFlex from "../../components/boxs/MainFlex";
import PageTitle from "../../components/font/PageTitle";
import { useCreateGroupInfo } from "../../hooks/groups"
  
  export default function GroupCreate() {
    const toast = useToast();
    const [name, setName] = React.useState("")
    const [desc, setDesc] = React.useState("")
    const [category, setCategory] = React.useState("")
    const handleGroupName = (event: any): any => setName(event.target.value)
    const handleGroupDesc = (event: any): any => setDesc(event.target.value)
    const handleGroupCategory = (event: any): any => setCategory(event.target.value)
    const { state, send } = useCreateGroupInfo();
    useEffect(()=>{
      if(state.status === "Mining") {
          toast({
              title: " ",
              position: "top-right",
              description: " .",
              status: "info",
              duration: 9000,
              isClosable: true,
            })
      }
      if(state.status === "Success") {
          toast({
              title: "  ",
              position: "top-right",
              description: "  !",
              status: "success",
              duration: 9000,
              isClosable: true,
            })
      }
      if(state.status === "Fail") {
          toast({
              title: " ",
              position: "top-right",
              description: state.errorMessage,
              status: "error",
              duration: 9000,
              isClosable: true,
            })
      }
      if(state.status === "Exception") {
          toast({
              title: "  ",
              position: "top-right",
              description: state.errorMessage,
              status: "error",
              duration: 9000,
              isClosable: true,
            })
      }
  },[state])

   function handleSetGroupInfo() {
        send(name, desc, 0)
      }
    return (
      <MainFlex>
        <Flex
            // justifyContent="center"
          justifyContent="flex-start"
        >
          <PageTitle text={"Create Group"} />
        </Flex>
            <Flex
              minW="250"
              backgroundColor="#FAFAFA"
              flexDirection="column"
              p="24px"
            >
              <Box
              >
                <Text>
                      Group Name
                </Text>
                <Input mt="2" onChange={handleGroupName} border="0" backgroundColor="white" variant="outline" placeholder="  ."/>
              </Box>
              <Box
                my="5"
              >
              <Text>
                      Group Desc
                </Text>
                <Textarea mt="2" onChange={handleGroupDesc} border="0" backgroundColor="white" h="200px" variant="outline" placeholder="  ."/>
              </Box>
          </Flex>
          <Flex
            justifyContent="center"
          >
          <Button
            minW="150"
                mt={{base: 5}}
                isLoading={state.status === "Mining" ? true : false}
                backgroundColor="purple.500"
                color="white"
                borderRadius="2px"
                _focus={{
                    border: "0"
                }}
                onClick={()=> handleSetGroupInfo()}
              >
                 
            </Button>
          </Flex>
          </MainFlex>
    );
  }
  