import { Box, Flex, Text } from "@chakra-ui/react";
import { Link } from "react-router-dom";
import { useGetProposalInfo, useProposalState } from "../../hooks/proposals";
import transferProposalState from "../../utils/transferProposalState";

export default function ProposalSampleInfo({proposalId, campaignId}: any){
    const proposalInfo = useGetProposalInfo(proposalId, campaignId);
    const proposalState = useProposalState(campaignId, proposalId);
    const StateBox = ({color, text}: any) =>(
        <Box 
        backgroundColor={color}
        borderRadius="md"
        pt="4px"
        px="10px"
        pb="2px"
    >
        <Text
            fontFamily="Poppins"
            fontWeight="medium"
            fontSize="12px"
            color="white"
            textAlign="center"
        >
            {text}
        </Text>
    </Box>
    )
    return (
        <Link to={`/proposalInfo?campaignId=${campaignId}&proposalId=${[proposalId]}`}>
            <Flex
                flexDirection="row"
                backgroundColor="gray.200"
                borderRadius="md"
                alignItems="center"
                justifyContent="space-between"
                px="2"
                py="1"
                mb="1"
            >
            <Flex>
                <Text
                    fontFamily="Poppins"
                    fontWeight="medium"
                    fontSize="15px"
                    color="black"
                >
                {!proposalInfo ? <></> :  proposalInfo.proposalTitle}
                </Text>
            </Flex>
            <Flex>
                {transferProposalState(proposalState) === "" ? <StateBox color="purple.500" text=""/>
                    : transferProposalState(proposalState) === "" ? <StateBox color="tomato" text=""/>
                    : transferProposalState(proposalState) === "" ? <StateBox color="red.500" text=""/>
                    : transferProposalState(proposalState) === "" ? <StateBox color="blue.500" text=""/>
                    : transferProposalState(proposalState) === "" ? <StateBox color="green.500" text=""/>
                    : transferProposalState(proposalState) === "" ? <StateBox color="gray.500" text=""/>
                    : <StateBox color="#000" text=""/>
                }
            </Flex>
            </Flex>
            </Link>
    )
}